@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

* {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4 ,h5, h6 {
  font-weight: 700;
}

a, a:hover {
  text-decoration: none;
  color: unset;
}

img {
  width: 100%;
  height: auto;
}